header {
  position: relative;
  height: 50px; }
nav {
  position: fixed;
  width: 100%;
  height: 50px;
  background-color: #212121;
  z-index: 5; }
.hamburger {
  display: none; }
.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;
  &_link {
    font-weight: 300;
    font-size: 15px;
    color: #FFFFFF;
    &:hover {
      text-decoration: none;
      color: $main-color; } }
  &_item {
    border-right: 2px dotted black;
    padding-right: 28px;
    &:last-child {
      border-right: none; } } }

.subheader {
  position: absolute;
  top: 50px;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);
  &_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }
  &_official {
    margin-top: 6px;
    opacity: 0.5;
    font-weight: 400;
    font-size: 13px;
    color: #FFFFFF; }
  &_call {
    color: #FFFFFF;
    font-size: 12px;
    font-family: 300;
    margin-top: 19px;
    text-align: right; }
  &_phone {
    display: block;
    margin-top: 8px;
    color: #FFFFFF;
    font-size: 22px;
    font-weight: 400;
    text-align: right;
    line-height: 12px; }
  &_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    background-image: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 400;
    color: #FFFFFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
    text-transform: uppercase;
    @include btn_hover; } }

.promo {
  min-height: 800px;
  background: url(../img/bg/main_bg.jpg) center (center / cover) no-repeat;
  padding: 150px 0 135px 0;
  &_header {
    font-weight: 700;
    font-size: 60px;
    text-transform: uppercase;
    color: #ffffff;
    line-height: 60px;
    margin: 0;
    text-align: center; }
  &_subheader {
    margin-top: 22px;
    font-weight: 700;
    font-size: 36px;
    text-transform: uppercase;
    color: #ffffff;
    line-height: 36px;
    margin: 0;
    text-align: center; }
  &_descr {
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    color: #FFFFFF;
    margin-top: 23px; }
  &_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    background-image: linear-gradient(to top, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
    @include btn_hover; } }

.reasons {
  padding: 40px 0 65px 0;
  &_subtitle {
    line-height: 21px;
    color: #1eacc7;
    font-size: 17px;
    font-weight: 400; }
  &_block {
    margin-top: 35px;
    display: flex;
    justify-content: center;
    align-items: center; }
  &_round {
    display: flex;
    margin-right: 26px;
    align-items: center;
    justify-content: center;
    width: 116px;
    height: 116px;
    border-radius: 100%;
    background: #1EACC7; }
  &_descr {
    width: 343px;
    &_subtitle {
      font-size: 17px;
      line-height: 21px;
      display: flex;
      font-weight: 400;
      color: #1EACC7; }
    &_text {
      color: #202020;
      font-size: 15px;
      font-weight: 300;
      line-height: 21px; } } }

.label {
  position: relative;
  margin: 0 auto;
  padding: 5px 7px;
  width: 115px;
  height: 27px;
  background: #000000;
  font-size: 12px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  z-index: 2;
  &_white {
    background-color: #fff;
    color: #000; } }
.title {
  position: relative;
  margin-bottom: 0;
  margin-top: 38px;
  font-size: 38px;
  line-height: 48px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  color: #222222;
  z-index: 2;
  &_white {
    color: #fff; } }

.subtitle {
  position: relative;
  margin-top: 19px;
  color: #222222;
  font-size: 17px;
  font-weight: 400;
  text-align: center;
  z-index: 2;
  &_white {
    color: #fff; } }

.mobile {
  min-height: 864px;
  padding: 48px 0 79px 0;
  background: url('../img/bg/mobile_app.png') center (center / cover) no-repeat;
  .row {
    margin-top: 32px; }
  &_item {
    position: relative;
    width: 320px;
    height: 280px;
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 92px;
    &_1 {
       background: url('../img/bg/mobile_screen_1.jpg') center (center / cover) no-repeat; }
    &_2 {
       background: url('../img/bg/mobile_screen_2.jpg') center (center / cover) no-repeat; }
    &_3 {
       background: url('../img/bg/mobile_screen_3.jpg') center (center / cover) no-repeat; }
    &_4 {
       background: url('../img/bg/mobile_screen_4.jpg') center (center / cover) no-repeat; }
    &_5 {
       background: url('../img/bg/mobile_screen_5.jpg') center (center / cover) no-repeat; }
    &_6 {
       background: url('../img/bg/mobile_screen_6.jpg') center (center / cover) no-repeat; }
    &_subtitle {
      color: #ffffff;
      font-size: 24px;
      font-weight: 400;
      text-transform: uppercase;
      line-width: 31px;
      text-align: center; }
    &_plus {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      bottom: 35px;
      left: 50%;
      transform: translateX(-50%);
      width: 29px;
      height: 29px;
      background: #000000;
      margin: 0 auto;
      border-radius: 100%;
      cursor: pointer;
      &:before {
        content: '';
        display: block;
        width: 14px;
        height: 2px;
        background-color: $main-color;
        transition: 0.3s all; }
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: block;
        height: 14px;
        width: 2px;
        background-color: $main-color;
        transition: 0.4s all; }
      &:hover {
        &:before, &:after {
          background-color: $second-color; } } } } }

.choice {
	position: relative;
	min-height: 895px;
	padding: 57px 0 138px 0;
	background: url('../img/bg/mobile_app.png') center (center / cover) no-repeat;
	&_img {
		position: relative;
		display: block;
		margin: 56px auto 0 auto;
		z-index: 2; }
	&_descr {
		text-align: center;
		color: #222222;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px; }
	.black {
		position: absolute;
		top: 0;
		left: 0;
		height: 499px;
		width: 100%;
		background-color: #070716;
		z-index: 1; } }

.require {
  padding: 68px 0 84px 0;
  &_block {
    margin: 33px 0;
    &_nmb {
      margin-bottom: 0px; } }
  &_title {
    font-size: 22px;
    font-weight: 400;
    color: #1CA8C3; }
  &_list {
    list-style-type: none;
    padding-left: 50px;
    margin-top: 22px;
    li {
      position: relative;
      font-weight: 300;
      font-size: 17px;
      line-height: 24px;
      margin-bottom: 10px;
      &:before {
        content: '';
        position: absolute;
        left: -32px;
        top: 3px;
        width: 21px;
        height: 20px;
        background: url('../icons/check_blue.png') center (center / cover) no-repeat; } } }
  &_descr {
    margin-top: 5px;
    font-size: 17px;
    font-weight: 300;
    line-height: 24px;
    color: blue; }
  .warning {
    .require_title {
      color: $second-color; }
    .require_list {
      li {
        &:before {
          background: url('../icons/check_red.png') center (center / cover) no-repeat; } } } } }
.world {
	position: relative;
	min-height: 717px;
	padding: 49px 0;
	background: url('../img/bg/map.jpg') center (center / cover) no-repeat;
	&_link {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 49px;
		text-align: center;
		font-size: 15px;
		font-weight: 400;
		line-height: 68px;
		text-transform: uppercase;
		color: #1fbad6;
		&:hover {
			text-decoration: none;
			color: #fff; } } }
.footer {
	&_divider {
		width: 299px;
		height: 4px;
		background-color: #1fbad6;
		margin: 0 auto; }
	&_wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px; }
	&_wrapper > div {
		width: 33%;
		&:nth-child(3) {
			display: flex;
			justify-content: flex-end; } }
	&_social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		&_item {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 4px;
			width: 32px;
			height: 32px;
			background-color: #070716;
			border-radius: 100%;
			i {
				color: #fff; } } }
	&_links {
		width: 390px;
		margin: 0 auto;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				color: #070716;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_sub {
			display: flex;
			justify-content: space-between;
			margin-top: 10px;
			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase; } }
		&_lang {
			display: block;
			margin: 0 auto;
			color: #1fbad6;
			font-size: 11px;
			font-weight: 400;
			margin-top: 19px;
			text-transform: uppercase;
			text-align: center; } } }
.xs-visible {
  display: none; }
